import { Button, JsonInput } from "@mantine/core";
import { showNotification } from "@mantine/notifications";
import React, { useRef } from "react";

export const JsonInputWrapper = ({ initValue, actionButtonText, onClickActionButton, disabled = false }) => {

    const jsonRef = useRef();

    const onClickActionButtonHandler = () => {
        const validJson = checkValidJson();

        if (validJson) {
            onClickActionButton(validJson);
            showNotification({ message: "Success", color: "green" });
        }
    }

    const checkValidJson = () => {
        if (!jsonRef.current.value) return;

        try {
            const json = JSON.parse(jsonRef.current.value);
            return json;
        } catch (e) {
            showNotification({ message: "Invalid JSON.", color: "red" });
        }
    }

    return (
        <div style={{ display: "flex", flexDirection: "column", width: "100%", height: "calc(100% - 20px)", justifyContent: "space-between" }}>
            <JsonInput
                formatOnBlur
                autosize={false}
                fullWidth
                ref={jsonRef}
                defaultValue={initValue}
                disabled={disabled}
                styles={{
                    wrapper: { height: '100%' },
                    input: { height: '100%' },
                    root: { height: '88%' },
                }}
                onBlur={checkValidJson}
            >

            </JsonInput>
            <Button variant="light" color={"bluegray"} fullWidth onClick={onClickActionButtonHandler}>{actionButtonText}</Button>
        </div >
    )
};