import { Badge, Button, Grid, Group, ScrollArea, SimpleGrid, Space, Title } from "@mantine/core";
import { showNotification } from "@mantine/notifications";
import React, { useContext, useEffect, useState } from "react";
import { FileDownload, FileUpload } from "tabler-icons-react";
import { CustomCard } from "../components/CustomCard";
import { EventStateContext } from "../components/EventProvider";
import { JsonInputWrapper } from "../components/JsonInputWrapper";
import { ParamsStateContext } from "../components/ParamsProvider";
import { ThemeChangeButton } from "../components/ThemeChangeButton";
import { WorkSessionEntry } from "../components/WorkSessionEntry";

export const IOPage = ({ visible }) => {

    const paramsContext = useContext(ParamsStateContext);
    const eventContext = useContext(EventStateContext);

    const [storage, setStorage] = useState();

    const onLoad = (data) => {
        console.log("onLoad", data.timestamp);

        try {
            paramsContext.importNew(data.params);
            eventContext.setImportedEventJson(data.events);
            eventContext.setImportedEventNodesJson(data.eventNodes);
            showNotification({ message: "Load session sucessfully.", color: "green" });
        }
        catch (e) {
            showNotification({ message: "Error importing data. Please contact the developer.", color: "red" });
            console.error(e);
        }
    }

    const onDelete = (timestamp) => {
        console.log("delete", timestamp);

        try {
            localStorage.removeItem(timestamp.toString());
            showNotification({ message: "Session deleted.", color: "green" });
        }
        catch (e) {
            showNotification({ message: "Error deleting data. Please contact the developer.", color: "red" });
            console.error(e);
        }

        window.dispatchEvent(new Event("storage"));
    }

    const saveSession = () => {
        const timestamp = Date.now();

        try {
            localStorage.setItem(timestamp.toString(), JSON.stringify(
                {
                    timestamp: timestamp,
                    events: eventContext.events,
                    eventNodes: eventContext.eventNodes,
                    params: paramsContext.data
                }
            ));
        }
        catch (e) {
            showNotification({ message: "Error saving data. Please contact the developer.", color: "red" });
            console.error(e);
        }

        window.dispatchEvent(new Event("storage"));
    }

    const refetch = () => {
        console.log("?")
        setStorage(Object.entries(localStorage));
    }

    useEffect(() => {
        refetch();

        window.addEventListener("storage", refetch);

        return () => {
            window.removeEventListener("storage", refetch);
        };
    }, []);

    return (
        <>
            <div style={{ display: visible ? "initial" : "none" }}>
                <div style={{ display: "flex", flexDirection: "column", width: "100%", height: "100%" }}>
                    <div style={{ display: "flex", justifyContent: "space-between", marginBottom: 10 }}>
                        <Title
                            order={2}
                            align="left"
                        >
                            CODE:A - IMPORT/EXPORT
                        </Title>
                        <Group spacing={"xs"}>
                            <ThemeChangeButton />
                        </Group>
                    </div>
                    <Grid gutter="xs" columns={12} style={{ height: "100%" }}>
                        <Grid.Col span={2}>
                            <CustomCard title={<>{"SESSION"}<Space w="xs" /><Badge size="xs" radius="sm" variant="gradient" gradient={{ from: 'blue', to: 'cyan' }}>Experimental</Badge></>}>
                                <ScrollArea
                                    style={{ position: "absolute", top: 4, bottom: 10, left: 0, right: -10 }}
                                    offsetScrollbars
                                    classNames={{
                                        viewport: 'viewport-override',
                                    }}
                                >
                                    <Group direction="column" grow spacing={"xs"}>
                                        {
                                            storage && storage.map(([key, json]) => {
                                                const data = JSON.parse(json);

                                                return <WorkSessionEntry
                                                    data={data}
                                                    timestamp={data.timestamp}
                                                    numOfParams={Object.entries(data.params).map(([key, value]) => Object.keys(value).length).reduce((a, b) => a + b, 0)}
                                                    numOfEvents={Object.keys(data.events).length}
                                                    numOfEventNodes={Object.keys(data.eventNodes).length}
                                                    onLoad={onLoad}
                                                    onDelete={onDelete}
                                                />
                                            }
                                            )
                                        }
                                        <Button variant="light" onClick={saveSession} fullWidth color={"bluegray"} style={{ textAlign: "center" }} compact>Save</Button>
                                    </Group>
                                </ScrollArea>
                            </CustomCard>
                        </Grid.Col>
                        <Grid.Col span={10}>
                            <Grid gutter="xs" columns={12} style={{ height: "101%" }}>
                                <Grid.Col span={12}>
                                    <SimpleGrid cols={3} spacing="xs" style={{ height: "100%" }}>
                                        <CustomCard title={<><FileUpload /><Space w="xs" />{" IMPORT PARAMS JSON"}</>}>
                                            <JsonInputWrapper
                                                actionButtonText={"Import"}
                                                onClickActionButton={paramsContext.importNew}
                                            />
                                        </CustomCard>
                                        <CustomCard title={<><FileUpload /><Space w="xs" />{"IMPORT EVENTS JSON"}</>}>
                                            <JsonInputWrapper
                                                actionButtonText={"Import"}
                                                onClickActionButton={eventContext.setImportedEventJson}
                                            />
                                        </CustomCard>
                                        <CustomCard title={<><FileUpload /><Space w="xs" />{"IMPORT EVENT NODES JSON"}</>}>
                                            <JsonInputWrapper
                                                actionButtonText={"Import"}
                                                onClickActionButton={eventContext.setImportedEventNodesJson}
                                            />
                                        </CustomCard>
                                    </SimpleGrid>
                                </Grid.Col>
                                <Grid.Col span={12}>
                                    <SimpleGrid cols={3} spacing="xs" style={{ height: "100%" }}>
                                        <CustomCard title={<><FileDownload /><Space w="xs" />{"EXPORT PARAMS JSON"}</>}>
                                            <JsonInputWrapper
                                                disabled
                                                actionButtonText={"Copy"}
                                                initValue={JSON.stringify(paramsContext.data, null, 2)}
                                                onClickActionButton={() => navigator.clipboard.writeText(JSON.stringify(paramsContext.data, null, 2))}
                                            />
                                        </CustomCard>
                                        <CustomCard title={<><FileDownload /><Space w="xs" />{"EXPORT EVENTS JSON"}</>}>
                                            <JsonInputWrapper
                                                disabled
                                                actionButtonText={"Copy"}
                                                initValue={JSON.stringify(eventContext.events, null, 2)}
                                                onClickActionButton={() => navigator.clipboard.writeText(JSON.stringify(eventContext.events, null, 2))}
                                            />
                                        </CustomCard>
                                        <CustomCard title={<><FileDownload /><Space w="xs" />{"EXPORT EVENT NODES JSON"}</>}>
                                            <JsonInputWrapper
                                                disabled
                                                actionButtonText={"Copy"}
                                                initValue={JSON.stringify(eventContext.eventNodes, null, 2)}
                                                onClickActionButton={() => navigator.clipboard.writeText(JSON.stringify(eventContext.eventNodes, null, 2))}
                                            />
                                        </CustomCard>
                                    </SimpleGrid>
                                </Grid.Col>
                            </Grid>
                        </Grid.Col>
                    </Grid>
                </div>
            </div>
        </>
    );
}